<template>
    <div>
        <div v-if="show" class="col-md-offset-2">
            <button @click="regresar" class="btn bg-purple">
                <span class="fa fa-arrow-left"></span>
                Regresar
            </button>
            <div class="h5">Listado de estudios</div>
            <div class="col-xs-12 col-md-8 row">
                <ul class="list-group">
                    <li class="list-group-item" v-for="(m,i) in labs" :key="m.id">
                        <div class="input-group">
                            {{m.producto.nombre}}
                            <span class="input-group-btn">
                                <button @click="check(i,m.id)" class="btn btn-sm" :class="m.finalizado?'btn-success':'btn-danger'" title="Cambiar">
                                    <span v-if="m.finalizado" class="fa fa-check-circle-o"></span>
                                    <span v-else class="fa fa-check-circle"></span>
                                </button>
                            </span>
                            <span class="input-group-addon">
                                <span v-if="m.finalizado" class="text-success">Finalizado</span>
                                <span v-else class="text-danger">Pendiente</span>
                            </span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <c-data-tables v-else @paginate="send" @refresh="refresh" title="Laboratorio" :limite="[10,20,50,100]" :total="total" :opt="opts">
            <template v-slot:table>
                <table class="table table-condensed table-sm table-hover">
                    <thead>
                        <tr>
                            <th class="col-xs-1">Codigo</th>
                            <th>Cliente</th>
                            <th class="col-xs-1">Modo</th>
                            <th class="col-xs-1">Estado</th>
                            <th>Fecha</th>
                            <th></th>
                        </tr>
                        <tr>
                        <th>
                            <div class="input-group">
                                <input @keyup.enter="send" v-model.number="values.id" type="number" class="form-control input-sm">
                                <span class="input-group-btn">
                                <button type="button" @click="values.id=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                            </div>
                        </th>
                        <th></th>
                        <th>
                            <select @change="send" v-model="values.modo" class="form-control input-sm">
                                <option value="null">Todos</option>
                                <option value="normal">Normal</option>
                                <option value="urg">Urgencia</option>
                                <option value="m1">Maquila 1</option>
                                <option value="m2">Maquila 2</option>
                            </select>
                        </th>
                        <th>
                            <select @change="send" v-model.number="values.finalizado" class="form-control input-sm">
                                <option value="null" class="label label-primary">Todos</option>
                                <option value="1" class="label label-success">Completado</option>
                                <option value="0" class="label label-danger">Pendiente</option>
                            </select>
                        </th>
                        <th>
                            <div class="input-group">
                                <input @keyup.enter="send" v-model="values.fecha" type="date" class="form-control input-sm">
                                <span class="input-group-btn">
                                <button @click="values.fecha=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                            </div>
                        </th>
                    </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(m) in model" :key="m.id">
                            <td>{{m.id}}</td>
                            <td>{{m.cliente.nombre}} ( <b>{{m.cliente.telefono}}</b> )</td>
                            <td>
                               <span v-if="m.modo==='normal'" class="label label-success">Normal</span>
                                <span v-else-if="m.modo==='urg'" class="label label-danger">Urgencia</span>
                                <span v-else-if="m.modo==='m1'" class="label label-warning">Maquila 1</span>
                                <span v-else-if="m.modo==='m2'" class="label label-info">Maquila 2</span>
                            </td>
                            <td>
                                <span v-if="m.finalizado" class="label label-success">Completado</span>
                                <span v-else class="label label-danger">Pendiente</span>
                            </td>
                            <td>{{m.created_at}}</td>
                            <td>
                                <button @click="editar(m.id)" class="btn btn-primary btn-sm"><span class="fa fa-edit"></span></button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </template>
        </c-data-tables>
    </div>
</template>

<script>
    import api from '@/services'

    export default {
        name: "laboratorio",
        data(){
            return {

                confirmar:{id:0,index:null},
                showModal:false,
                search:false,
                model:[],
                values: {
                    id:null,
                    client_id:null,
                    cobrado_por:null,
                    modo:null,
                    fecha:null,
                    finalizado:null
                },///
                show:false,
                readyData:false,
                paciente:'',
                labs:[],///
                total:0,
                opts:{
                    per_page:10,
                    page:1,
                }
            }
        },
        mounted(){
            this.$emit('title','Laboratorio');
        },
        methods: {
            //////tabla
            buscarModal(){
                this.limpiar()
                this.search = true;
            },
            limpiar(){
                this.values.id = null;
                this.values.client_id = null;
                this.values.cobrado_por = null;
                this.values.fecha = null;
                this.values.modo = null;
            },
            send(){
                api.ventas.search(this.getParams()).then((r)=>{
                    if (r.status ===200){
                        this.model = r.data.data;
                        this.total = r.data.total;
                    }
                }).catch(()=>{});
            },
            refresh(){
                this.limpiar();
                this.send();
            },
            getParams(){
                return {...this.opts,params:this.values};
            },
            editar(id){
                this.buscar(id);
                this.show = true;
            },
            //////listado de laboratorios
            regresar(){
                this.show = false;
                this.lab = [];
            },
            //buscar los estudios para el id seleccionado
            buscar(id){
                api.detalle.get(id).then((r)=>{
                    if (r){
                        this.labs = r.data;
                        this.readyData = true;
                    }
                }).catch(()=>{});
            },
            check(index,id){
                if (this.labs[index].finalizado){
                    this.submit(index,id,false);
                } else {
                    this.submit(index,id,true);
                }
            },
            submit(index,id,estado){
                api.lab.check(id,{estado:estado}).then((r)=>{
                    if (r.data == 1){
                        this.labs[index].finalizado = estado;
                    }else {
                        this.$toast.warning('Error al guardar, Intente de nuevo');
                    }
                });
            },
        }
    }
</script>